.hamburger{
    background-color: transparent;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.whileClose{
    background-color: darkorange;
    display: inline-block;
    width: 44px;
    height: 4px;
    -webkit-transition: all .5s linear;
    -moz-transition: all .5s linear;
    -o-transition: all .5s linear;
    -ms-transition: all .5s linear;
    transition: all .5s linear;
    position: relative;
    margin-top: 10px;
    border-radius:8px;
    &::before{
        content: "";
        position: absolute;
        display: inline-block;
        top: -12px;
        left: 4px;
        background-color: #3ecfff;
        height: 4px;
        width: 36px;
        transform: scale(1);
        -webkit-transition: all .5s linear;
        -moz-transition: all .5s linear;
        -o-transition: all .5s linear;
        -ms-transition: all .5s linear;
        transition: all .5s linear;
        border-radius:8px;
    }
    &::after{
        content: "";
        position: absolute;
        display: inline-block;
        bottom: -12px;
        left: 4px;
        background-color: #3ecfff;
        height: 4px;
        width: 36px;
        transform: scale(1);
        -webkit-transition: all .5s linear;
        -moz-transition: all .5s linear;
        -o-transition: all .5s linear;
        -ms-transition: all .5s linear;
        transition: all .5s linear;
        border-radius:8px;
    }
}

.whileOpen{
    background-color: transparent;
    display: inline-block;
    width: 44px;
    height: 4px;
    position: relative;
    margin-top: 10px;
    -webkit-transition: all .5s linear;
    -moz-transition: all .5s linear;
    -o-transition: all .5s linear;
    -ms-transition: all .5s linear;
    transition: all .5s linear;
    border-radius:8px;
    &::before{
        content: "";
        position: absolute;
        display: inline-block;
        -webkit-transform: rotate(35deg) scale(1);
        -moz-transform: rotate(35deg) scale(1);
        -ms-transform: rotate(35deg) scale(1);
        -o-transform: rotate(35deg) scale(1);
        transform: rotate(35deg) scale(1);
        background-color: darkorange;
        height: 4px;
        width: 36px;
        -webkit-transition: all .5s linear;
        -moz-transition: all .5s linear;
        -o-transition: all .5s linear;
        -ms-transition: all .5s linear;
        transition: all .5s linear;
        border-radius:8px;
    }
    &::after{
        content: "";
        position: absolute;
        display: inline-block;
        -webkit-transform: rotate(-35deg) scale(1);
        -moz-transform: rotate(-35deg) scale(1);
        -ms-transform: rotate(-35deg) scale(1);
        -o-transform: rotate(-35deg) scale(1);
        transform: rotate(-35deg) scale(1);
        background-color: darkorange;
        height: 4px;
        width: 36px;
        -webkit-transition: all .5s linear;
        -moz-transition: all .5s linear;
        -o-transition: all .5s linear;
        -ms-transition: all .5s linear;
        transition: all .5s linear;
        border-radius:8px;
    }
}


@media only screen and (min-width:768px){
    .hamburger{
        display: none;
    } 
}