@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&display=swap');

.home-card{
    display: grid;
    grid-template-rows: 2fr 4fr 5fr 1fr;
    grid-template-columns: 1fr 4fr 5px 4fr 1fr;
    .text{
        grid-row: 2/4;
        grid-column: 2/3;
        justify-self: center;
        align-self: center;
        font-size: 18px;
        z-index: 1;
        #hello{
            font-size: 20px;
            font-weight: 600;
        }
        #ozgur{
            font-size: 36px;
            font-family: 'Montserrat Alternates', sans-serif;
            font-weight: 500;
            color: rgb(255, 140, 0);
        }
        .iletisim{
            margin: 0;
            position: relative;
            p{
                background-color: darkorange;
                color: #070620;
                cursor: pointer;
                border-radius: 4px;
                font-weight: 900;
                width: 100px;
                text-align: center;
                padding: 8px 0;
                margin: 0;
                margin-top: 24px;
                z-index: 1;
                -webkit-touch-callout: none;
                -webkit-user-select: none;
                -khtml-user-select: none; 
                -moz-user-select: none; 
                -ms-user-select: none;
                user-select: none;
            }
            p::after{
                content: "";
                position: absolute;
                top: 10px;
                left: 0;
                width: 10px;
                height: 1%;
                border-radius: 4px;
                background-color: darkorange;
                z-index: -1;
                -webkit-transition: height .6s ease-in;
                -moz-transition: height .6s ease-in;
                -o-transition: height .6s ease-in;
                -ms-transition: height .6s ease-in;
                transition: height .6s ease-in;
            }
            
            .iletisim-container{
                box-sizing: border-box;
                .iletisim-link-bir{
                    background-color: darkorange;
                    margin: 10px 0;
                    padding: 6px 16px;
                    border-radius: 4px;
                    display: flex;
                    width: 60%;
                    align-items: center;
                    overflow: hidden;
                    transform: scaleX(.18);
                    transform-origin: left;
                    -webkit-transition: all .5s ease-in .2s;
                    -moz-transition: all .5s ease-in .2s;
                    -o-transition: all .5s ease-in .2s;
                    -ms-transition: all .5s ease-in .2s;
                    transition: all .5s ease-in .2s;
                    .iletisim-link-a{
                        opacity: 0;
                        text-decoration: none;
                        color: transparent;
                        margin: 0;
                        padding: 0;
                        font-size: 14px;
                        font-weight: 500;
                        margin-left: 16px;
                        -webkit-transition: all .1s ease-in .5s;
                        -moz-transition: all .1s ease-in .5s;
                        -o-transition: all .1s ease-in .5s;
                        -ms-transition: all .1s ease-in .5s;
                        transition: all .1s ease-in .5s;
                        -webkit-touch-callout: all;
                        -webkit-user-select: all;
                        -khtml-user-select: all; 
                        -moz-user-select: all; 
                        -ms-user-select: all;
                        user-select: all;
                    }
                    .iletisim-link-icon{
                        opacity: 0;
                        -webkit-transition: all .5s ease-in .5s;
                        -moz-transition: all .5s ease-in .5s;
                        -o-transition: all .5s ease-in .5s;
                        -ms-transition: all .5s ease-in .5s;
                        transition: all .5s ease-in .5s;
                    }
                }
                .iletisim-link-iki{
                    background-color: darkorange;
                    margin: 10px 0;
                    padding: 6px 16px;
                    border-radius: 4px;
                    width: 50%;
                    display: flex;
                    align-items: center;
                    overflow: hidden;
                    transform: scaleX(.12);
                    transform-origin: left;
                    -webkit-transition: all .5s ease-in .3s;
                    -moz-transition: all .5s ease-in .3s;
                    -o-transition: all .5s ease-in .3s;
                    -ms-transition: all .5s ease-in .3s;
                    transition: all .5s ease-in .3s;
                    .iletisim-link-a{
                        opacity: 0;
                        text-decoration: none;
                        color: transparent;
                        margin: 0;
                        padding: 0;
                        font-size: 14px;
                        font-weight: 500;
                        margin-left: 16px;
                        -webkit-transition: all .1s ease-in .5s;
                        -moz-transition: all .1s ease-in .5s;
                        -o-transition: all .1s ease-in .5s;
                        -ms-transition: all .1s ease-in .5s;
                        transition: all .1s ease-in .5s;
                    }
                    .iletisim-link-icon{
                        opacity: 0;
                        -webkit-transition: all .5s ease-in .5s;
                        -moz-transition: all .5s ease-in .5s;
                        -o-transition: all .5s ease-in .5s;
                        -ms-transition: all .5s ease-in .5s;
                        transition: all .5s ease-in .5s;
                    }
                }
                .iletisim-link-uc{
                    background-color: darkorange;
                    margin: 10px 0;
                    padding: 6px 16px;
                    border-radius: 4px;
                    display: flex;
                    width: 40%;
                    align-items: center;
                    overflow: hidden;
                    transform: scaleX(.07);
                    transform-origin: left;
                    -webkit-transition: all .5s ease-in .4s;
                    -moz-transition: all .5s ease-in .4s;
                    -o-transition: all .5s ease-in .4s;
                    -ms-transition: all .5s ease-in .4s;
                    transition: all .5s ease-in .4s;
                    .iletisim-link-a{
                        opacity: 0;
                        text-decoration: none;
                        color: transparent;
                        margin: 0;
                        padding: 0;
                        font-size: 14px;
                        font-weight: 500;
                        margin-left: 16px;
                        -webkit-transition: all .1s ease-in .5s;
                        -moz-transition: all .1s ease-in .5s;
                        -o-transition: all .1s ease-in .5s;
                        -ms-transition: all .1s ease-in .5s;
                        transition: all .1s ease-in .5s;
                    }
                    .iletisim-link-icon{
                        opacity: 0;
                        -webkit-transition: all .5s ease-in .5s;
                        -moz-transition: all .5s ease-in .5s;
                        -o-transition: all .5s ease-in .5s;
                        -ms-transition: all .5s ease-in .5s;
                        transition: all .5s ease-in .5s;
                    }
                }
            }
            &:hover{
                p::after{
                    height: 90%;
                }
                .iletisim-container{
                    .iletisim-link-bir{
                        transform: scaleX(1);
                        .iletisim-link-a{
                            color: black;
                            opacity: 1;
                        }
                        .iletisim-link-icon{
                            opacity: 1;
                        }
                    }
                    .iletisim-link-iki{
                        transform: scaleX(1);
                        .iletisim-link-a{
                            color: black;
                            opacity: 1;
                        }
                        .iletisim-link-icon{
                            opacity: 1;
                        }
                    }
                    .iletisim-link-uc{
                        transform: scaleX(1);
                        .iletisim-link-a{
                            color: black;
                            opacity: 1;
                        }
                        .iletisim-link-icon{
                            opacity: 1;
                        }
                    }
                }
            }
        }
        
    }
    
    .resim{
        grid-row: 2/4;
        grid-column: 4/5;
        justify-self: flex-end;
        align-self: flex-end;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none; 
        -moz-user-select: none; 
        -ms-user-select: none;
        user-select: none;
        .img1{
            width: 100%;
        }
    }
   
}

@media only screen and (max-width:"576px") {
    .home-card{
        grid-template-rows: 1fr 4fr 2fr;
        grid-template-columns: 1fr;
        .text{
            grid-row: 2/4;
            grid-column: 1/2;
            font-size: 16px;
            
        }
        .resim{
            grid-row: 2/4;
            grid-column: 1/2;
        }
    }
}
@media only screen and (min-width:"576px") and (max-width:"768px") {
    .home-card{
        grid-template-rows: 1fr 4fr 2fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        .text{
            grid-row: 2/4;
            grid-column: 1/3;
            font-size: 16px;
            
        }
        .resim{
            grid-row: 2/4;
            grid-column: 2/4;
        }
    }
}
@media only screen and (min-width:"768px") and (max-width:"992px") {
    .home-card{
        grid-template-rows: 2fr 4fr 2fr;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        .text{
            grid-row: 2/4;
            grid-column: 1/4;
            font-size: 16px;
            
        }
        .resim{
            grid-row: 2/4;
            grid-column: 3/6;
        }
    }
}
@media only screen and (min-width:"992px") and (max-width:"1200px") {
    .home-card{
        grid-template-rows: 2fr 4fr 2fr 1fr;
        grid-template-columns: 1fr 5px 1fr;
        .text{
            grid-row: 2/4;
            grid-column: 1/2;
            font-size: 18px;
            
        }
        .resim{
            grid-row: 2/4;
            grid-column: 3/4;
        }
    }
}