.app-container{
    width: 100%;
    //background-image: linear-gradient(#070620, #070c36);
    background-image: url("./Rain.png");
    display: grid;
    grid-template-rows: minmax(50px, 100px) auto 50px;
    grid-template-columns: 1fr 5fr 1fr;
    min-height: 100vh;
    z-index: 2;
}
.header{
    grid-column: 2/3;
    grid-row: 1/2;
}
.main{
    grid-column: 2/3;
    grid-row: 2/3;
}
.footer{
    grid-column: 2/3;
    grid-row: 3/4;
}

