@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&display=swap');


.navbar{
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo{
        height: 50%;
        width: auto;
        margin-left: 2em;
        cursor: pointer;
    }
    .navbar-nav{
        width: auto;
        height: auto;
        list-style: none;
        margin-right: 2em;
        display: flex;
        justify-content: center;
        align-items: center;
        .nav-item{
            margin-left: 3em;
            width: auto;
            cursor: pointer;
            .nav-link{
                text-decoration: none;
                font-size: 18px;
                color: #24c7ff;
                font-family: 'Montserrat Alternates', sans-serif;
                font-weight: 500;
                text-shadow: 0 0 0;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -5px;
                    height: 3px;
                    width: 100%;
                    display: block;
                    background-color: #24c7ff;
                    transform: scaleX(0);
                    transform-origin: right;
                    -webkit-transition: transform .5s ease-in;
                    -moz-transition: transform .5s ease-in;
                    -o-transition: transform .5s ease-in;
                    -ms-transition: transform .5s ease-in;
                    transition: transform .5s ease-in;
                }
                &:hover{
                    color: #24c7ff;
                    text-shadow: 0 0 50px #24c7ff, 0 0 75px #24c7ff,  0 0 100px #24c7ff,  0 0 125px #24c7ff;
                }
                &:hover:before{
                    background-color: #24c7ff;
                    transform: scaleX(1);
                    transform-origin: left;
                }
            }
        }
        .selectedItem{
            position: relative;
            width: auto;
            height: auto;
            .nav-link{
                text-decoration: none;
                font-size: 1em;
                color: darkorange;
                font-family: 'Montserrat Alternates', sans-serif;
                font-weight: 500;
                text-shadow: 0 0 0;
                position: relative;
                cursor: none;
                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -2px;
                    height: 1px;
                    width: 100%;
                    display: block;
                    background-color: darkorange;
                    transform: scaleX(1);
                }
                &:hover{
                    color: darkorange;
                    text-shadow: 0 0 50px darkorange, 0 0 75px darkorange,  0 0 100px darkorange,  0 0 125px darkorange;
                }
                &:hover:before{
                    background-color: darkorange;
                    transform: scaleX(1);
                }
            }
        }           
    }
}

@media only screen and (max-width:768px){
    .navbar{
        height: 50px;
        .logo{
            -webkit-transform: rotate(-90deg);
            -moz-transform: rotate(-90deg);
            -ms-transform: rotate(-90deg);
            -o-transform: rotate(-90deg);
            transform: rotate(-90deg);
            height: 100%;
            margin-top: 12px;
        }
        .navbar-nav{
            display: none;
        }
    }
}


    
